.el-button {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #FFFFFF;
  border: 1px solid #DCDFE6;
  border-color: #DCDFE6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
  margin: 0;
  -webkit-transition: .1s;
  transition: .1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
.el-button + .el-button {
    margin-left: 10px;
}
.el-button.is-round {
    padding: 12px 20px;
}
.el-button:hover, .el-button:focus {
    color: #5E7CE2;
    border-color: #cfd8f6;
    background-color: #eff2fc;
}
.el-button:active {
    color: #5570cb;
    border-color: #5570cb;
    outline: none;
}
.el-button::-moz-focus-inner {
    border: 0;
}
.el-button [class*="el-icon-"] + span {
    margin-left: 5px;
}
.el-button.is-plain:hover, .el-button.is-plain:focus {
    background: #FFFFFF;
    border-color: #5E7CE2;
    color: #5E7CE2;
}
.el-button.is-plain:active {
    background: #FFFFFF;
    border-color: #5570cb;
    color: #5570cb;
    outline: none;
}
.el-button.is-active {
    color: #5570cb;
    border-color: #5570cb;
}
.el-button.is-disabled, .el-button.is-disabled:hover, .el-button.is-disabled:focus {
    color: #C0C4CC;
    cursor: not-allowed;
    background-image: none;
    background-color: #FFFFFF;
    border-color: #EBEEF5;
}
.el-button.is-disabled.el-button--text {
    background-color: transparent;
}
.el-button.is-disabled.is-plain, .el-button.is-disabled.is-plain:hover, .el-button.is-disabled.is-plain:focus {
    background-color: #FFFFFF;
    border-color: #EBEEF5;
    color: #C0C4CC;
}
.el-button.is-loading {
    position: relative;
    pointer-events: none;
}
.el-button.is-loading:before {
      pointer-events: none;
      content: '';
      position: absolute;
      left: -1px;
      top: -1px;
      right: -1px;
      bottom: -1px;
      border-radius: inherit;
      background-color: rgba(255, 255, 255, 0.35);
}
.el-button.is-round {
    border-radius: 20px;
    padding: 12px 23px;
}
.el-button.is-circle {
    border-radius: 50%;
    padding: 12px;
}
.el-button--primary {
